import { Component, OnInit, ViewChild, ChangeDetectorRef, NgZone } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MediaMatcher } from '@angular/cdk/layout';
import { ScrollDispatcher } from '@angular/cdk/scrolling';
import { MatSidenav } from '@angular/material/sidenav';
import { Router, NavigationEnd } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { filter, debounce } from 'rxjs/operators';
import { AuthService } from './service/auth.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit{
  @ViewChild('sidenav') sidenav: MatSidenav;
  readonly shortTitle = 'æ¥“æ—æ™š';
  readonly title = 'æ¥“æ—æ™š - è¿½åŠ‡ é›»å½± ç·šä¸Šçœ‹';

  menuTree = [
    { name: 'é¦–é ', link:'/index', showInToolbar: false },
    { name: 'é›»å½±', link:'/category/movies' },
    { name: 'å°åŠ‡', link:'/category/taiwan' },
    { name: 'æ—¥åŠ‡', link:'/category/japan' },
    { name: 'éŸ“åŠ‡', link:'/category/korea' },
    { name: 'ç¾åŠ‡', link:'/category/usa' },
    { name: 'é™¸åŠ‡', link:'/category/china' },
    { name: 'å‹•ç•«', link:'/category/anime' },
  ];
  toolbarMenu = this.menuTree.filter(i => i.showInToolbar !== false );
  isLoading = false;
  isInMainPage: boolean;
  isScrolled = false;

  constructor(
    private router: Router,
    private snackBar: MatSnackBar,
    private titleService: Title,
    private changeDetectorRef: ChangeDetectorRef,
    private media: MediaMatcher,
    private scrollDispatcher: ScrollDispatcher,
    private zone: NgZone,
    private authService: AuthService,
    ) {
    this.mobileQuery = this.media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => this.changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
    this.scrollDispatcher.scrolled(100).subscribe( () =>
      this.isInMainPage && this.zone.run(() => this.isScrolled = document.scrollingElement.scrollTop > 0));
  }

  ngOnInit() {
    // for Github Page's routing hack with 404.html
    let routePath = sessionStorage.getItem('routePath');
    if( routePath ) {
      sessionStorage.removeItem('routePath');
      this.router.navigate([routePath]);
    }

    // gdpr cookie policy
    if( document.cookie.search(/accept-cookie/i) === -1 ) {
      let snackBarRef = this.snackBar.open('æœ¬ç¶²ç«™ä½¿ç”¨cookiesä»¥ææ˜‡æ‚¨çš„ä½¿ç”¨é«”é©—åŠçµ±è¨ˆã€‚ç¹¼çºŒä½¿ç”¨æœ¬ç¶²ç«™è¡¨ç¤ºæ‚¨åŒæ„æˆ‘å€‘ä½¿ç”¨cookiesã€‚',
      'åŒæ„', { horizontalPosition: 'right'});
      snackBarRef.afterDismissed().subscribe(() => {
        document.cookie = 'accept-cookie=true;';
      });
    }

    // when route changes
    this.router.events
    .pipe(filter( e => e instanceof NavigationEnd ))
    .subscribe( (e:NavigationEnd) => {
      // scroll to top
      window.scrollTo(0, 0);

      // judge if it's in main page
      this.isInMainPage = e.url === '/' || e.url === '/index';

      // change title
      const match = this.menuTree.find( i => i.link === e.url);
      this.titleService.setTitle( match ? (match.name + ' - ' + this.shortTitle ) : this.title  );
    });
  }
  mobileQuery: MediaQueryList;
  private _mobileQueryListener: () => void;

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  closeSidenav(): void {
    this.sidenav.close();
  }

  login() {
    this.authService.doGoogleLogin();
  }
}
